<template>
  <div>
    <div class="container">
      <Navbar/>
      <Alert></Alert>
      <div class="container-fluid">
        <div class="row">
   
          <main role="main" class="col-md-9 ml-sm-auto col-lg-12 px-4">
            <router-view></router-view>
          </main>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Sidebar from "./Sidebar";
import Navbar from "./Navbar";
import Alert from "./AlertMessage";
export default {
  components: {
    Sidebar,
    Navbar,
    Alert
  }
};
</script>

<style>
.container {
  width: 1450px;
}
</style>



